.Post {
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    text-align: start;
    &__drscr {
        display: flex;
        align-items: center;
        gap: 10px;
        &__item {
            max-width: 700px;
            display: block;
            &__img {
                width: 45px;
                height: 45px;
                border-radius: 30px;
                object-fit: cover; }
            &__title {
                margin-left: 15px;
                font-size: 26px;
                color: #222222;
                font-weight: 700; }
            &__text {

                font-family: 'monospace', sans-serif;
                word-break: break-all;
                overflow-wrap: break-word;
                text-align: left;
                -ms-hyphens: auto;
                -webkit-hyphens: auto;
                hyphens: auto;
                margin-top: 8px;
                display: flex;
                gap: 10px;
                font-size: 21px;
                font-weight: 500; }

            &__like {
                display: flex;
                gap: 10px; }
            &__dislike {
                display: flex;
                gap: 10px; } }
        &__wrap {
            display: flex;
            gap: 20px;
            margin-top: 10px;
            align-items: center; } } }
